import React from 'react';
import withCustomAuthenticator from '../Authenticator/withCustomAuthenticator';
import qs from 'query-string';
import { Auth } from 'aws-amplify';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';

import PrimaryQuestionnaireDashboard from './PrimaryQuestionnaireDashboard';
import GeneralInfoDashboard from './GeneralInfoDashboard';
import MarketingDashboard from './MarketingDashboard';
import PaymentDashboard from './PaymentDashboard';
import FinanceTaxTitleDashboard from './FinanceTaxTitleDashboard';
import SystemsFeaturesDashboard from './SystemsFeaturesDashboard';
import Listing from './Listing';
import ShowingDashboard from './ShowingDashboard';

const Actions = [
  { Action: 'listing', Component: Listing },
  { Action: 'pq', Component: PrimaryQuestionnaireDashboard },
  { Action: 'showing', Component: ShowingDashboard },
  { Action: 'systemsfeatures', Component: SystemsFeaturesDashboard },
  { Action: 'financetaxtitle', Component: FinanceTaxTitleDashboard },
  { Action: 'payment', Component: PaymentDashboard },
  { Action: 'marketing', Component: MarketingDashboard },
  { Action: 'generalinfo', Component: GeneralInfoDashboard }
];

class Dashboard extends React.Component {
  constructor (props) {
    super();

    const queryString = qs.parse(props.location.search, { ignoreQueryPrefix: true });
    const action = queryString.action;
    const propertyId = queryString.propertyid;

    this.state = {
      propertyId: propertyId,
      action: action
    };
  }

  render () {
    const action = Actions.find(action => action.Action === this.state.action);

    const componentProps = {
      PropertyId: this.state.propertyId,
      Action: this.state.action,
      Auth
    };

    return (
      <BlockUi
        tag='div'
        blocking={this.state.Loading}
        loader={<Loader active type='ball-pulse' color='rgba(231,28,99,1)' />}
      >
        {
          action === undefined &&
            <div style={{ minHeight: '80vh', marginTop: '120px' }}>
              <p className='incorrect_parameter' style={{ textAlign: 'center' }}>Incorrect Parameters</p>
            </div>
        }

        {
          action !== undefined &&
            <div className='container'>
              <action.Component {...componentProps} />
            </div>
        }
      </BlockUi>
    );
  }
}

export default withCustomAuthenticator(Dashboard);
